import type { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from '@reduxjs/toolkit';
import { DefaultLang } from 'utils/constants';

type Lang = 'vi' | 'en';

export interface I18State {
  lang: Lang;
}

const initialState: I18State = {
  lang: DefaultLang,
};

export const i18Slice = createSlice({
  name: 'i18n',
  initialState,
  reducers: {
    change: (state, action: PayloadAction<Lang>) => {
      state.lang = action.payload;
    },
  },
});

// Action creators are generated for each case reducer function
export const { change } = i18Slice.actions;

export default i18Slice.reducer;
