import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import { DefaultLang } from 'utils/constants';
import en from './_en';
import vi from './_vi';

const i18next = i18n;

const resources = {
  en: {
    translation: en,
  },
  vi: {
    translation: vi,
  },
};

i18next.use(initReactI18next).init({
  resources,
  lng: DefaultLang,
  fallbackLng: DefaultLang,
  keySeparator: '.',
  interpolation: {
    escapeValue: false,
  },
});

export default i18next;
